.landingPage .register {
    display: block;
    margin: auto;
    width: 15%;
    margin-top: 20%;
}
.cover{
    width: 100%;
}
.about {
    font-size: 15px;
    color: #000;
}
.landingPage .storyWrap, .landingPage .valueWrap {
    padding: 100px 0 165px;
}
.storyWrap .left, .valueWrap .left {
    float: left;
    width: 560px;
    margin-right: 30px;
    margin-bottom: 45px;
}
.storyWrap .heading{
    font-family: sans-serif;
    font-size: 60px;
    font-weight: 100;
    letter-spacing: .1em;
    line-height: 1em;
    color: #db3236;
}
.valueWrap .heading{
    font-family: sans-serif;
    font-size: 60px;
    font-weight: 100;
    letter-spacing: .1em;
    line-height: 1em;
    color: #3cba54;
}
.storyWrap .right, .valueWrap .right{
    float: right;
    width: 50%;
    margin-bottom: 25px;
}
.storyWrap .right p, .valueWrap .right p {
    font-size: 16px;
    font-weight: 300;
    letter-spacing: .025em;
    line-height: 1.5em;
    color: #000;
    margin: 0;
}
.storyWrap .right p+p, .valueWrap .right p+p {
    margin: 80px 0 0;
}
.landingPage .parrel{
    width: 100%;
    height: 90vh;
    margin-top: 50px;
    background-size: cover;
}
.valueWrap .title {
    font-size: 24px;
    font-weight: 200;
    letter-spacing: .025em;
    color: #000;
    margin-bottom: 30px;
}
.valueWrap .title:nth-child(3) {
    margin-top: 80px;
}
.valueWrap .title:nth-child(5) {
    margin-top: 80px;
}
.sponsWrap{
    background: #333332;
    padding: 1px 0;
    max-width: 100%;
    width: 100%;
    overflow: hidden;
    margin-top: 5%;
}
.spons {
    margin-left: 15%;
}
.cont {
    margin-top: 5%;
    padding-left: 20%;
}
.clearFix{
    display: table;
    width: 100%;
    margin: 114px 0 123px;
}
.clearFix .heading{
    display: table-cell;
    vertical-align: top;
    text-align: left;
    font-size: 50px;
    font-weight: bold;
    color: #fff;
    line-height: 1em;
    width: 40%;
    padding-right: 30px;
    font-family: sans-serif;
    padding-left: 15%;
}
.clearFix .heading+div{
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}
.clearFix p {
    color: #fff;
    font-size: 24px;
    font-weight: 300;
    letter-spacing: .025em;
    line-height: 1.4em;
    margin-bottom: 21px;
}
.clearFix h4{
    font-size: 18px;
    font-weight: 300;
    color: #fff;
    letter-spacing: .025em;
    background: #0078D7;
    -webkit-transition: background-color .2s cubic-bezier(.4,0,.2,1);
    -o-transition: background-color .2s cubic-bezier(.4,0,.2,1);
    transition: background-color .2s cubic-bezier(.4,0,.2,1);
    text-align: center;
    display: inline-block;
    height: 40px;
    line-height: 40px;
    width: 145px;
    margin-right: auto;
    border-radius: 5px;
}

            /*Mobile*/
@media only screen and  (max-width : 767px){ 
    .storyWrap .right, .valueWrap .right{
        width: 100%;
        margin-bottom: 25px;
        padding: 1px 10px;
        text-align: justify;
    }
    .spons {
        margin-left: 0%;
    }
    .cont {
        margin-top: 8%;
        padding-left: 28%;
    }
    .clearFix {
        display: table;
        width: 100%;
        margin: 35px 0 123px;
    }
    .landingPage .parrel{
        width: 100%;
        height: 50vh;
        margin-top: 50px;
        background-size: cover;
    }
}


